.blogs {
  padding: 2em;
  margin: 0 auto;
  text-align: left;
  box-shadow: var(--shadow);
  transition: transform 0.2s linear;
}

.blogs__section__title {
  text-align: left;
  margin-top: 1em;
  margin-bottom: 1em;
}

.blogs__grid {
  max-width: 1000px;
  margin: 0 auto;
  display: block;
}

.blogs__grid>div {
  margin-bottom: 2em;
}